import React, { useCallback, useEffect, useState } from 'react';
import '../components/style.css';
import WeatherDetails from './WeatherDetails';

function SearchMain() {
  const [searchTerm, setSearchTerm] = useState('San Francisco');
  const [tempInfo, setTempInfo] = useState({});

  const apiKey = '679e2b244b4efa1d9b420c236490aec7';

  const getWeatherInfo = async () => {
    try {
      let url = `https://api.openweathermap.org/data/2.5/weather?q=${searchTerm}&units=metric&appid=${apiKey}`;

      let res = await fetch(url);
      let data = await res.json();
      const { temp, humidity, pressure } = data.main;
      const { main: weatherType } = data.weather[0];
      const { name } = data;
      const { speed } = data.wind;
      const { country, sunset } = data.sys;

      const myNewWeatherInfo = {
        temp,
        humidity,
        pressure,
        weatherType,
        name,
        speed,
        country,
        sunset,
      };

      setTempInfo(myNewWeatherInfo);
      // console.log(data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getWeatherInfo();
  }, []);

  return (
    <>
      <div className='wrap'>
        <div className='search'>
          <input
            type='search'
            placeholder='Enter City'
            id='search'
            value={searchTerm}
            onChange={(event) => setSearchTerm(event.target.value)}
          />
          <button className='searchButton' onClick={getWeatherInfo}>
            SEARCH
          </button>
        </div>
      </div>
      {/* This the the weather details page */}
      <WeatherDetails {...tempInfo} />
    </>
  );
}

export default SearchMain;
